<template lang="pug">
	.colors
		.glide__track(data-glide-el="track")
			.glide__slides
				.color.glide__slide(v-for="color in colors", @click="goColor(color)")
					input(type="radio",
						:value="color",
						name="font", 
						v-model="currentColors")
					.icon(:class="'theme-'+color")
					
</template>
<script>
import Glide from '@glidejs/glide'

export default {
	name: 'ColorsCarousel',
	data() {
		return{
            currentColors: '',
			slideColors: null,
			colors: ['blue', 'green',  'yellow', 'greenlight', 'red']
		}
	},
    props: {
		current: {
			type: String,
			default: ''
		}
	},
	computed: {
	},
	mounted(){
		this.slideColors = new Glide('.colors', {
			type: 'slider',
			focusAt: 'center',
			perView: '4'
		})

		this.slideColors.mount()

		this.slideColors.on('swipe.end', (data) => {
            this.currentColors = this.colors[this.slideColors.index];
			this.$emit('update', this.colors[this.slideColors.index]);
		})

		this.goColor(this.current)
	},
	components: {
	},
	methods: {
		goColor(item){
			let index = this.colors.indexOf(item);
			this.slideColors.go('='+index);

            // this.currentColors = this.colors[this.slideColors.index];
			this.$emit('update', this.colors[this.slideColors.index]);
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/components/InputText.styl"></style>