<template lang="pug">
	.send
		user-modal(@close="closeModalUser", :class="modalUser ? 'open' : ''")
		.frame(:class="['theme-'+tools.theme, loadingView ? 'loading' : '']")
			.border
				.top
				.bottom
				
			.header
				.user
					.photo
						img(:src="currenUser.photo")
					span.name {{currenUser.name}}
				.tools
					.align(@click="alignText", :class="tools.align")
					.themes(v-if="currentTool === 'fonts'", @click="changeColors")
					.fonts(v-else @click="changeFonts")

			.wrap-input(:class="['align-'+tools.align, 'font-'+tools.font]")
				textarea-autosize(
					class="input-text",
					placeholder="Deixe sua mensagem...",
					v-model="message",
					@blur.native="blurInput",
					@focus.native="focusInput")

				span.description Envie um elogio, um carinho ou uma piada
					
		.footer
			.options
				colors-carousel(
					:current="tools.theme",
					:class="currentTool === 'colors' ? 'show' : ''", 
					@update="getColor")
				fonts-carousel(
					:current="tools.font",
					:class="currentTool === 'fonts' ? 'show' : ''", 
					@update="getFont")
			
			.buttons
				button.button-users(@click="openModalUser")
				button.button-send(
					@click="send", 
					:class="[loading ? 'loading' : '', message.length >= 5 ? '' : 'disable']") {{loading ? 'Enviando' : 'Enviar'}} para {{firstName}}
					
</template>
<script>
import Vue from 'vue'
import TextareaAutosize from 'vue-textarea-autosize'
import ColorsCarousel from '@/components/ColorsCarousel'
import UserModal from '@/components/UserModal'
import FontsCarousel from '@/components/FontsCarousel'

Vue.use(TextareaAutosize)

import { doc, updateDoc, orderBy, arrayUnion, collection, query, where, getFirestore, getDocs } from "firebase/firestore";

export default {
	name: 'SendView',
	data() {
		return{
			loadingView: false,
			modalUser: false,
			currentTool: 'fonts',
			tools: {
				align: 'left',
				font: 'teko',
				theme: 'red'
			},
			loading: false,
			message: '',
			currenUserRef: '',
			currenUser: ''
		}
	},
	components: {
		ColorsCarousel,
		UserModal,
		FontsCarousel
	},
	computed: {
		firstName(){
			return this.currenUser ? this.currenUser.name.split(' ')[0] : ''
		}
	},
	created(){
		// this.fetchUser();
	},
	mounted() {
		this.fetchUser();
		this.$root.$el.classList.remove('openMenu')
	},
	beforeRouteEnter (to, from, next) {
		if(to.params.username){
			next()
		}else{
			next('/');
		}
	},
	beforeRouteUpdate(to, from, next){
		next();
		this.fetchUser();
	},
	methods: {
		closeModalUser(){
			this.modalUser = false
		},
		openModalUser(){
			this.modalUser = true
		},
		getColor(data){
			this.tools.theme = data;
		},
		getFont(data){
			this.tools.font = data;
		},
		alignText(){
			switch (this.tools.align) {
				case 'left':
					this.tools.align = 'center'
					break;
				case 'center':
					this.tools.align = 'right'
					break;
				default:
					this.tools.align = 'left'
			}
		},
		changeColors(){
			this.currentTool = 'colors'
		},
		changeFonts(){
			this.currentTool = 'fonts'
		},
		focusInput(){
			this.$root.$el.classList.add('overlay')
		},
		blurInput(){
			this.$root.$el.classList.remove('overlay')
		},
		onInput(e) {
			this.message = '';
			this.message = e.target.innerText;
		},
		async fetchUser(){
			this.loadingView = true;
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			// Create a query against the collection.
			const q = query(usersRef, where('username', '==', this.$route.params.username));

			const querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				this.currenUserRef = doc.id;
				this.currenUser = doc.data();
				
				this.loadingView = false;
			});
		},
		clearData(){
			this.message = '';
		},
		async send(){
			this.loading = true;
			const db = getFirestore();
			const user = doc(db, 'users', this.currenUserRef);
			
			await updateDoc(user, {
				messages: arrayUnion({text: this.message, configs: this.tools})
			});

			let tempSend = {
				message: this.message,
				auth: this.currenUser,
				tools: this.tools
			}

			this.$store.commit('setCurrentSend', tempSend);

			this.loading = false;
			this.clearData();

			this.$router.push({name: 'success'})
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Send.styl"></style>