<template lang="pug">
	.success(:class="['theme-'+currentSend.tools.theme, 'font-'+currentSend.tools.font]")
		.header
			p Sua mensagem foi enviada para <strong>{{currentSend.auth.name}}</strong> :)
		
		.content-success
			.ilustration
				img(:src="currentSend.auth.photo")
				.icon
				
			.message
				p {{currentSend.message}}

		.list
			.user(v-for="user in users", @click="goUser(user.username)")
				img.photo(:src="user.photo")
				span.name {{user.name}}
			
</template>

<script>
// @ is an alias to /src
import { collection, query, where, orderBy, getFirestore, getDocs } from "firebase/firestore";
import { mapGetters } from 'vuex'

export default {
	name: 'HomeView',
	props: ['data'],
	data (){
		return{
			users: []
		}
	},
	computed: {
		...mapGetters(['currentSend'])
	},
	components: {
	},
	created(){
		this.fetchUsers();
	},
	mounted() {
	},
	methods: {
		goUser(username){
			this.$router.push({name: 'send', params: {username: username}})
		},
		async fetchUsers(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');

			const q = query(usersRef, orderBy("name"));

			let querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				this.users.push(doc.data());
			});
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Success.styl"></style>
