<template lang="pug">
	.schedules
		//- button(@click="createAgenda") Vai
		.schedule(v-for="schedule in schedules")
			span.day {{schedule.title}}
			.list
				.item(v-for="item in schedule.schedules")
					span.title {{item.title}}
					span.description(v-html="item.description")
</template>

<script>
// @ is an alias to /src
import { collection, addDoc, getFirestore, getDocs } from "firebase/firestore";

export default {
	name: 'HomeView',
	data (){
		return{
			schedules: [],
			tempschedules: [
				{
					title: '22/06/2022',
					schedules: [
						{
							title: '9:00 as 12:30',
							description: '<strong>Imersão: </strong>bora refletir sobre como os clientes nos veem, nossos produtos e estratégias?'
						},
						{
							title: 'Almoço'
						},
						{
							title: '14:00 as 18:00',
							description: '<strong>Ação: </strong>vem com a gente para pensarmos juntos em soluções pros nossos principais desafios?'
						}
					]
				},
				{
					title: '21/06/2022',
					schedules: [
						{
							title: '09:00 as 12:30',
							description: '<strong>Conexão: </strong>um momento pra gente se conectar como time e se conhecer.'
						},
						{
							title: 'Almoço'
						},
						{
							title: '09:00 as 12:30',
							description: '<strong>Reflexão: </strong>é hora de pensar em como estamos e projetar nossa evolução enquanto time.'
						}
					]
				}
			]
		}
	},
	components: {
	},
	created(){
		this.fetchSchedules();
	},
	mounted() {
		this.$root.$el.classList.remove('openMenu')
	},
	methods: {
		createAgenda(){
			const db = getFirestore();

			this.tempschedules.forEach(schedule => {
				const docRef = addDoc(collection(db, "agenda"), {
					title: schedule.title,
					schedules: schedule.schedules
				});
			});
		},
		async fetchSchedules(){
			const db = getFirestore();
			const schedulesRef = collection(db, 'agenda');

			let querySnapshot = await getDocs(schedulesRef);
			querySnapshot.forEach((doc) => {
				this.schedules.push(doc.data());
			});
		}
	}
}
</script>
