<template lang="pug">
	.fonts
		.glide__track(data-glide-el="track")
			.glide__slides
				.font.glide__slide(v-for="font in fonts", @click="goFont(font)")
					input(type="radio",
						:value="font",
						name="font", 
						v-model="currentFont")
					.icon(:class="'font-'+font")
						span Aa
					
</template>
<script>
import Glide from '@glidejs/glide'

export default {
	name: 'FontsCarousel',
	data() {
		return{
			currentFont: this.current,
			slideFonts: null,
			fonts: ['teko','zen','zilla']
		}
	},
	props: {
		current: {
			type: String,
			default: ''
		}
	},
	computed: {
	},
	mounted(){
		this.slideFonts = new Glide('.fonts', {
			type: 'slider',
			focusAt: 'center',
			perView: '4'
		})

		this.slideFonts.mount()

		this.slideFonts.on('swipe.end', (data) => {
			// console.log(slide.index)
			this.currentFont = this.fonts[this.slideFonts.index];
			this.$emit('update', this.fonts[this.slideFonts.index]);
		})

		this.goFont(this.current)
	},
	components: {
	},
	methods: {
		goFont(item){
			let index = this.fonts.indexOf(item);
			this.slideFonts.go('='+index);

			// this.currentFont = this.fonts[this.slideFonts.index];
			this.$emit('update', this.fonts[this.slideFonts.index]);
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/components/InputText.styl"></style>