<template lang="pug">
	.users-modal
		.overlay(@click="close")
		.users
			.list
				.user(v-for="user in users", @click="goUser(user.username)")
					img.photo(:src="user.photo")
					span.name {{user.name}}
					
</template>
<script>
import { collection, orderBy, query, getFirestore, getDocs } from "firebase/firestore";
export default {
	data() {
		return{
			users: []
		}
	},
	computed: {
	},
	mounted(){
		this.fetchUsers();
	},
	components: {
	},
	methods: {
		close(){
			this.$emit('close');
		},
		goUser(username){
			this.$router.push({name: 'send', params: {username: username}})
			this.$emit('close');
		},
		async fetchUsers(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			
			const q = query(usersRef, orderBy("name"));

			let querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				if (doc.data().username != 'sfpj') {
					this.users.push(doc.data());
				}
			});
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/components/UserModal.styl"></style>